import React,  {Fragment} from "react";
import {NavLink } from "react-router-dom";
import "./Header.css"
import logo from "./logo.png"

const Header = () => {
    return(
        <Fragment>
            <nav className="myHeader navbar navbar-expand-lg navbar-dark bg-dark justify-content-between">
                <div className="container">
                    <div>
                        <NavLink  to="/" className="navbar-brand"><img  src={logo} alt=""/></NavLink >
                    </div>

                    <div className="collapse navbar-collapse" id="navbarNavDropdown">
                        <ul className="navbar-nav">

                            <li className="nav-item ">
                                <NavLink activeClassName="active"  className=" nav-link" to="/productapi">Products by API</NavLink>
                            </li>

                            <li className="nav-item ">
                                <NavLink activeClassName="active"  className=" nav-link" to="/shop">Products by json</NavLink>
                            </li>


                            <li className="nav-item">
                                <NavLink  activeClassName="active"  className="nav-link" to="/todo">To Do</NavLink>
                            </li>

                            <li className="nav-item">
                                <NavLink activeClassName="active"  className="nav-link" to="/team">Team</NavLink>
                            </li>

                            <li className="nav-item">
                                <NavLink activeClassName="active"  className="nav-link" to="/blog">Blog</NavLink>
                            </li>


                        </ul>
                    </div>
                </div>


            </nav>
        </Fragment>
    )
}

export default Header;