import React, {Fragment} from "react";
import "./tobanner.css"
import heroImg from "./post6.jpg"

const ToBanner = (data)=> {
    return(
        <Fragment>
            <div style={{backgroundImage: `url(${data.background ? data.background : heroImg})`}} className="topBanner">
                <div className="container-fluid">
                    <div className="row">
                        <div className="col-md-12">
                           <div className="topBanner__heading">
                               <h2>{data.heading ? data.heading : "Insert the title."}</h2>
                           </div>
                        </div>
                    </div>
                </div>
            </div>

        </Fragment>
    )
}


export default ToBanner;