import React, {Fragment, useEffect, useState} from "react";
import ToBanner from "../hero/TopBanner";
import bannerImg from "./shopBanner.webp";
import Product from "./Product";

const ProductApi = ()=> {


        const [products, setProducts] = useState([]);
        useEffect(() => {
            fetch("https://fakestoreapi.com/products")
                .then(data=>data.json())
                .then(data=> setProducts(data))

        }, []);




    return(
        <Fragment>
            <ToBanner background={bannerImg} heading={"Product from api"} />
            <div className="container">
                <div className="row">
                    {
                        products.map((data)=>   <Product  products={data} />)
                    }


                </div>
            </div>
        </Fragment>
    )
}

export default ProductApi;