import React, {Component, Fragment} from 'react';
import Product from "./Product";
import products from "./products";
import ToBanner from "../hero/TopBanner";
import bannerImg from "./shopBanner.webp"

class Shop extends Component {
    render() {

        return (
            <Fragment>
                <ToBanner background={bannerImg} heading={'Product from json'} />
                <div className="container">
                    <div className="row">
                        {
                            products.map((data)=>   <Product  products={data} />)
                        }


                    </div>
                </div>

            </Fragment>
        );
    }
}

export default Shop;