import Ract, {Fragment} from "react";
import img from "./profileImage.jpg"
import "./team.css"

const User = (data)=> {
    return(
        <Fragment>
            <div className="col-md-6">
             <div className="card my-4 shadow">
                 <div className="card-body">
                     <div className="team ">
                         <div className="img">
                             <img className="img-fluid" src={data.info.avatar_url} alt="Img"/>
                         </div>
                        <div className="info">
                            <h4><a target="_blank" href={data.info.html_url}>{data.info.login}</a></h4>
                            <p>Lorem ipsum dolor sit amet, consectetur adipisicing elit. Aliquid cum dolorem</p>
                            <a target="_blank" href={data.info.html_url} className="btn btn-dark">About</a>
                        </div>
                     </div>
                 </div>
             </div>
            </div>
        </Fragment>
    )
}

export default User;