 import React, {Fragment} from "react";
import "./product.css";

let Product = (data)=> {

    return (
      <Fragment>
          <div className="col-md-3">
              <div className="product">
                  <img className="img-fluid" src={data.products.image} alt="img"/>
                  <h4>{data.products.title.length > 10 ? `${data.products.title.substr(0, 10)}...` : data.products.title}</h4>
                  <p>{data.products.description.length > 50 ? `${data.products.description.substr(0, 50)}...` : data.products.description}</p>
                  <h6>${data.products.price}</h6>
                  <a href="#" className="btn btn-dark"> ADD TO CART</a>
              </div>
          </div>
      </Fragment>
    );
}




export default Product;

