import React, {Fragment} from "react";
import "./todo.css"

const TodoItems = (data)=>{




    return(


        <Fragment>
            <div status={ data.status? 'done' : 'incompleted'  }  className="card todoItem shadow mb-2" >
                <div className="card-body d-flex">
                    <h4 >{data.todoItems.id}</h4>
                    <h4>{data.todoItems.title}</h4>

                </div>
            </div>
        </Fragment>
    )
}

export default TodoItems;