import React, {Fragment, useEffect, useState} from "react";
import User from "./User";
import ToBanner from "../hero/TopBanner";
import bannerImg from "./header2_1944x.png"


const Team = ()=> {
    const [users, setState] = useState([]);
    useEffect(() => {


        fetch("https://api.github.com/users")
            .then((data)=>data.json())
            .then(data=> {
                setState(data);
            })



    }, []);


    return(
        <Fragment>
            <ToBanner background={bannerImg} heading={"User from github api"} />
            <div className="container">
                <div className="row">
                    {
                        users.map((info)=> <User info={info} /> )
                    }


                </div>
            </div>
        </Fragment>
    )
}

export default Team;