import React, {Fragment} from "react";
import postImg from "./post.jpg"
import postAuthor from "./profileImage.jpg"
import "./post.css";

const Post = (data) => {
    return(
        <Fragment>
            <div className="col-md-12 mb-4 col-lg-6">
                <div className="post">
                   <div className="post__img">
                       <img className="img-fluid" src={postImg} alt=""/>
                   </div>
                    <div className="post__content">
                        <h4>{data.post.title}</h4>
                        <p>{data.post.body}</p>
                        <div className="post__author">
                            <div className="post__author-img">
                                <img className="img-fluid" src={postAuthor} alt=""/>
                            </div>
                            <div className="post__author-info">
                                    <h5 className="post__author-name">Milon</h5>
                                    <p className="post__author-role">Developer</p>
                            </div>
                        </div>

                    </div>
                </div>
            </div>
        </Fragment>
    )
}

export default Post;